#header_review {
	height: rem(226);
	background: url(../img/page_review/header_bg_x1.jpg) no-repeat;
	background-size: cover;

	

	.header__bottom {
		padding-bottom: 0;
		h1 {
			font-size: rem(22);
			font-weight: normal;
			max-width: rem(248);

			margin-top: rem(28);
			margin-bottom: rem(88);
		}
	}
}

@media only screen and (-Webkit-min-device-pixel-ratio: 1.5),
only screen and (-moz-min-device-pixel-ratio: 1.5),
only screen and (-o-min-device-pixel-ratio: 3/2),
only screen and (min-device-pixel-ratio: 1.5) {
	#header_review {
		background: url(../img/page_review/header_bg_x2.jpg) no-repeat;
		background-size: cover;
	}
}