#header_store {
	height: rem(181);
	background: url(../img/page_store/header_bg_x1.jpg) no-repeat;
	background-size: cover;

	> .container {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: rem(54);
			width: rem(300);
			height: rem(74);
			background: url(../img/page_store/header_i_x1.png) no-repeat top center;
		}
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(20);
		padding-bottom: 0;

		h1 {
			font-size: rem(22);
			font-weight: normal;
			margin-bottom: rem(20);
			max-width: 100%;
		}

	}
}

@media only screen and (-Webkit-min-device-pixel-ratio: 1.5),
only screen and (-moz-min-device-pixel-ratio: 1.5),
only screen and (-o-min-device-pixel-ratio: 3/2),
only screen and (min-device-pixel-ratio: 1.5) {
	#header_store {
		background: url(../img/page_store/header_bg_x2.jpg) no-repeat;
		background-size: cover;
	}
}